export default {
    created(){
     
    },
    data() {
        return {
            value: "",
            showfeelback: false,
            v:plus.runtime.version
        }
    },
    methods: {
        async post() {
            if (this.value.length == 0) {
                uni.showToast({
                    title: "说点什么吧...",
                    icon: "none"
                })
                return;
            }
            if (this.value.length <= 5) {
                uni.showToast({
                    title: "内容太少了",
                    icon: "none"
                })
                return;
            }
            await this.$u.post('/feedback', {
                feedback_content: this.value
            }).then(res => {
                if (res.errorCode !== 0) return;
                uni.showToast({
                    title: '感谢您的反馈'
                })
                return;
            })
        },
        async update(v) {
            //版本判断
            await this.$u.post('updateV', {
                v
            }).then(res => {
                if (res.errorCode === 0) {
                    // 新版本
                    uni.showModal({
                        title: '探索到新版本',
                        content: res.data.content,
                        success: (model)=>{
                            if (model.confirm) {
                              plus.runtime.openURL(res.data.url);
                            } else if (model.cancel) {
                                uni.showToast({
                                    title: "建议更新到最新版本",
                                    icon: "none"
                                })
                            }
                        }
                    });
                }
                if (res.errorCode === 20011) {
                    uni.showToast({
                        title: "当前已是最新版本",
                        icon: "none"
                    })
                }

            })
        }
    }
}
